<template>
  <div class="operationCenter" id="operationCenter">
    <el-container>
      <el-main>

        <el-row>
          <el-col :span="4">
            <el-aside width="200px">
              <el-radio-group v-model="isCollapse" style="margin-bottom: 2px;">
                <el-radio-button :label="false">展开</el-radio-button>
                <el-radio-button :label="true">收起</el-radio-button>
              </el-radio-group>
              <el-menu class="el-menu-vertical-demo" @open="handleOpen" @close="handleClose" :collapse="isCollapse" :router="true">
                <el-submenu index="enterprise">
                  <template slot="title">
                    <i class="el-icon-location"></i>
                    <span slot="title">企业</span>
                  </template>
                  <el-menu-item index="/operationcenter/archives">企业档案</el-menu-item>
                </el-submenu>
                <el-submenu index="merchant">
                  <template slot="title">
                    <i class="el-icon-location"></i>
                    <span slot="title">商户</span>
                  </template>
                  <el-menu-item index="/operationcenter/merchantList">商户列表</el-menu-item>
                </el-submenu>
                <el-submenu index="member">
                  <template slot="title">
                    <i class="el-icon-location"></i>
                    <span slot="title">会员</span>
                  </template>
                  <el-menu-item index="/operationcenter/enterprisenumberarchives">企业会员档案</el-menu-item>
                  <el-menu-item index="/operationcenter/enterprisenumberlist">企业会员列表</el-menu-item>
                  <el-menu-item index="/operationcenter/personalnumberarchives">个人会员档案</el-menu-item>
                  <el-menu-item index="/operationcenter/personalnumberlist">个人会员列表</el-menu-item>
                </el-submenu>
                <el-submenu index="agent">
                  <template slot="title">
                    <i class="el-icon-location"></i>
                    <span slot="title">代理商</span>
                  </template>
                  <el-menu-item index="/operationcenter/consultingrecord">咨询记录</el-menu-item>
                  <el-menu-item index="/operationcenter/servicedictionary">服务字典</el-menu-item>
                </el-submenu>
                <el-submenu index="archives">
                  <template slot="title">
                    <i class="el-icon-location"></i>
                    <span slot="title">档案</span>
                  </template>
                  <el-menu-item index="/operationcenter/archiveslist">档案列表</el-menu-item>
                </el-submenu>
                <el-submenu index="messsage">
                  <template slot="title">
                    <i class="el-icon-location"></i>
                    <span slot="title">消息</span>
                  </template>
                  <el-menu-item index="/operationcenter/messagelist">消息列表</el-menu-item>
                </el-submenu>
                <el-submenu index="decrypt">
                  <template slot="title">
                    <i class="el-icon-location"></i>
                    <span slot="title">解密</span>
                  </template>
                  <el-menu-item index="/operationcenter/decrypt">解密</el-menu-item>
                </el-submenu>
                <el-submenu index="enterthemerchant">
                  <template slot="title">
                    <i class="el-icon-location"></i>
                    <span slot="title">入驻商户</span>
                  </template>
                  <el-menu-item index="/operationcenter/enterthemerchantexamine">商户入驻审核</el-menu-item>
                  <el-menu-item index="/operationcenter/enterthemerchantlist">入驻商户列表</el-menu-item>
                  <el-menu-item index="/operationcenter/memberbalancesearch">会员余额查询</el-menu-item>
                  <el-menu-item index="/operationcenter/balanceflowsearch">余额流水查询</el-menu-item>
                </el-submenu>
              </el-menu>
            </el-aside>
          </el-col>
          <el-col :span="20">
            <el-breadcrumb separator="/" style="padding:20px;">
              <template v-for="route in routeList">
                <!--<span>{{!route.path?'/':route.path.match(/:/)?route.path.replace(/:\S+/,''):route.path}},</span>-->
                <el-breadcrumb-item :to="{path:!route.path?'/':route.path.match(/:/)?route.path.replace(/:\S+/,''):route.path}">{{route.name}}</el-breadcrumb-item>
              </template>
            </el-breadcrumb>
            <router-view></router-view>
          </el-col>
        </el-row>
      </el-main>
    </el-container>
  </div>
</template>

<script>
  export default {
    name: 'operationCenter',
    data() {
      return {
        activeName: '',
        isCollapse: false,
        routeList:[]
      }
    },
    created(){
      this.$set(this,'routeList',this.$route.matched)
    },
    methods: {
      handleClick(tab, event) {

      },
      handleOpen(key, keyPath) {
        console.log(key, keyPath);
      },
      handleClose(key, keyPath) {
        console.log(key, keyPath);
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .el-header, .el-footer {
    /*background-color: #B3C0D1;*/
    color: #333;
    background-color: #3fbc9c;
    text-align: left;
  }

  .el-aside {
    overflow: inherit;
  }
  .el-main {
    background-color: #E9EEF3;
    color: #333;
    text-align: center;
    line-height: 160px;
  }

  body > .el-container {
    margin-bottom: 40px;
  }

  .el-container:nth-child(5) .el-aside,
  .el-container:nth-child(6) .el-aside {
    line-height: 260px;
  }

  .el-container:nth-child(7) .el-aside {
    line-height: 320px;
  }
  .el-button {
    height: 60px;
  }
  .el-menu-vertical-demo:not(.el-menu--collapse) {
    width: 200px;
    min-height: 400px;
  }
  .el-radio-group {
    display: inherit;
  }
</style>



